/* StarRating.css */
.star {
  font-size: 30px;
  color: #ddd; /* 비활성화된 별의 색상 */
  display: inline-block;
  position: relative;
  border-radius: 70%;
  text-shadow: 0px 0px 3px rgba(0, 0, 0, 0.3);
}

.star.active {
  color: gold; /* 활성화된 별의 색상 */
}

.star.half-active {
  position: relative;
  display: inline-block;
}

.star.half-active .partial {
  color: gold;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.star.half-active .empty {
  color: #ddd;
}
